import axios, { AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useAuthHeader, useAuthUser } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router'

import {
  Box,
  Button,
  FormControl,
  InputLabel,
  ListSubheader,
  MenuItem,
  TextField as MuiTextField,
  Paper,
  Select,
  type SelectChangeEvent,
  TextFieldProps,
  TextareaAutosize,
  Typography,
  useMediaQuery
} from '@mui/material'
import { styled, useTheme } from '@mui/material/styles'

import departments from 'src/data/departments'
import { HELPER_TEXT_MESSAGE } from 'src/data/messages'

import { formatLikePhoneNumber } from 'src/utils/formatLikePhoneNumber '

import type { Applicant } from 'src/models/Applicant'
import type { ClubMemberForAuth } from 'src/models/ClubMember'
import type { Form } from 'src/models/Form'
import type { Recruit } from 'src/models/Recruit'

type FormAPIResponse = Form

type ApplyAPIResponse = Applicant['answers']

const Apply: React.FC = () => {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const { enqueueSnackbar } = useSnackbar()

  const [questions, setQuestions] = useState<string[]>([])
  const [recruitId, setRecruitId] = useState<Recruit['id']>()
  // TODO : id 달지 않고 들어오는 경우 처리
  const { id } = useParams()
  const clubId = Number(id)
  const authHeader = useAuthHeader()
  const navigate = useNavigate()
  const authUser = useAuthUser()
  const [values, setValues] = useState({
    name: authUser()?.name,
    phoneNumber: authUser()?.phoneNumber,
    department: authUser()?.department,
    answers: [] as Array<string> // TODO: api 분리하면서 응답값 타입으로 useState 지정하기
  })

  // 이미 회원인경우 접근 차단
  const isMember = authUser()?.clubs.filter((c: ClubMemberForAuth) => c.id === clubId).length > 0
  if (isMember) {
    enqueueSnackbar('이미 가입된 동아리입니다.', { variant: 'error' })
    navigate(-1)
  }

  const handleChange = (
    event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement> | SelectChangeEvent<string>
  ) => {
    const { name, value } = event.target
    setValues(prevValues => ({ ...prevValues, [name]: value }))

    if (event.target instanceof HTMLInputElement && event.target.name === 'name') {
      const name = event.target
      const trimmedName = name.value.trim()
      const regex = /^[a-zA-Z0-9ㄱ-ㅎㅏ-ㅣ가-힣 ]*$/
      const isValid = regex.test(name.value)

      if (trimmedName !== name.value) {
        name.setCustomValidity('이름 앞뒤에 공백문자를 포함할 수 없습니다.')
        return
      }
      if (!isValid) {
        name.setCustomValidity('이름에 특수문자를 포함할 수 없습니다.')
        return
      }
      name.setCustomValidity('')
    }
  }

  const handleAnswerChange: React.ChangeEventHandler<HTMLTextAreaElement> = ({ target: { name, value } }) => {
    setValues(prev => {
      const index = Number(name)
      const nextAnswers = [...prev.answers]
      nextAnswers[index] = value
      return { ...prev, answers: nextAnswers }
    })
  }

  const handlePhoneNumberChange: React.ChangeEventHandler<HTMLTextAreaElement> = ({ target: { value } }) => {
    setValues(prevState => ({ ...prevState, phoneNumber: formatLikePhoneNumber(value) }))
  }
  const handleSubmit: React.FormEventHandler<HTMLFormElement> = e => {
    e.preventDefault()

    const body = Object.assign({}, values)

    axios
      .post<ApplyAPIResponse>(`${process.env.REACT_APP_APIHOST}/apply/form?recruitId=${recruitId}`, body, {
        headers: { Authorization: authHeader() }
      })
      .then(() => {
        enqueueSnackbar('동아리에 지원서를 제출했어요! 좋은 결과 있기를 바라요 :)', { variant: 'success' })
        navigate(`/club/${clubId}`)
      })
      .catch(err =>
        enqueueSnackbar(err.response.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
      )
  }

  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/`)
      .then((res: AxiosResponse<Recruit>) => {
        setRecruitId(res.data.id)

        const startAt = new Date(res.data.startAt)
        const endAtNextDay = new Date(res.data.endAt)
        const today = new Date()

        // 마감일 23시 59분 59초까지 지원할 수 있도록 하루 더함
        endAtNextDay.setDate(endAtNextDay.getDate() + 1)

        // 지원 기간 내가 아닐 경우 접근 차단
        if (!(startAt <= today && today <= endAtNextDay)) {
          enqueueSnackbar('지금은 지원 기간이 아닙니다.', { variant: 'error' })
          navigate(-1)
        }
      })
      .catch(err => {
        enqueueSnackbar(`${err.response.data?.message}`, { variant: 'error' })
        navigate(-1)
      })

    // TODO : url 뒤에 / 떼면 301 Moved Permanently 에러뜸
    axios
      .get<FormAPIResponse>(`${process.env.REACT_APP_APIHOST}/club/${clubId}/form/`)
      .then(res => {
        const { questions } = res.data
        setQuestions(questions)
      })
      .catch(err =>
        enqueueSnackbar(err.response.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
      )
  }, [])

  return (
    <PageWrapper>
      <Helmet>
        <title>동아리 지원</title>
      </Helmet>
      <Paper
        square={matches ? false : true}
        sx={{
          my: theme.spacing(8),
          padding: 3,
          flex: 1,
          maxWidth: '480px',
          [theme.breakpoints.down('sm')]: {
            my: 0,
            py: 5,
            maxWidth: '100%'
          }
        }}
      >
        <Typography component="h1" variant="h3" sx={{ mb: 2, textAlign: 'center' }}>
          동아리 지원서
        </Typography>
        <Box
          component="form"
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            '& .MuiTextField-root': { margin: 1 }
          }}
          onSubmit={handleSubmit}
        >
          <TextField
            id="email"
            type="email"
            label="이메일"
            disabled
            defaultValue={authUser()?.email}
            inputProps={{ readOnly: true }}
          />
          <TextField
            id="student-id"
            type="text"
            label="학번"
            disabled
            defaultValue={authUser()?.studentId}
            inputProps={{ readOnly: true }}
          />
          <TextField
            id="name"
            name="name"
            type="text"
            label="이름"
            defaultValue={authUser()?.name}
            onChange={handleChange}
          />
          <TextField
            id="phone-number"
            name="phoneNumber"
            type="tel"
            label="전화번호"
            inputProps={{ pattern: '010-[0-9]{4}-[0-9]{4}' }}
            defaultValue={authUser()?.phoneNumber}
            helperText={HELPER_TEXT_MESSAGE.PHONE_NUMBER}
            value={values.phoneNumber}
            onChange={handlePhoneNumberChange}
          />
          <FormControl sx={{ margin: 1, width: '100%' }} size="small" required>
            <InputLabel htmlFor="department">학과/학부</InputLabel>
            <Select
              id="department"
              name="department"
              label="학과/학부"
              defaultValue={authUser()?.department || ''}
              onChange={handleChange}
              fullWidth
            >
              {departments &&
                Object.keys(departments).map(college => [
                  <ListSubheader sx={{ fontWeight: 'bold' }}>{college}</ListSubheader>,
                  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                  // @ts-ignore
                  departments[college] &&
                    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                    // @ts-ignore
                    departments[college].map((depart: string) => [
                      <MenuItem value={college === depart ? depart : college + ' ' + depart}>{depart}</MenuItem>
                    ])
                ])}
            </Select>
          </FormControl>
          {!authUser()?.phoneNumber && (
            <Box>
              <Typography variant="body1" display="inline">
                입력하신 정보는 동아리 관리를 위해 서버에 저장됩니다.
              </Typography>
            </Box>
          )}

          {questions?.filter(q => q).length > 0 && (
            <>
              <Typography variant="h6" sx={{ marginTop: 1, marginBottom: theme.spacing(0.5) }}>
                지원서 질문
              </Typography>
              {questions.map((question, index) => (
                <Box width="100%" key={index} sx={{ marginBottom: 1 }}>
                  <Typography variant="subtitle1" sx={{ marginBottom: theme.spacing(0.5), textAlign: 'left' }}>
                    {question}
                  </Typography>
                  <CustomTextareaAutosize minRows={4} maxRows={6} name={`${index}`} onChange={handleAnswerChange} />
                </Box>
              ))}
            </>
          )}
          <Button type="submit" variant="contained" fullWidth sx={{ mt: 1 }}>
            지원서 제출
          </Button>
        </Box>
      </Paper>
    </PageWrapper>
  )
}

export default Apply

const PageWrapper = styled(Box)(
  () => `
        display: flex;
        justify-content: center;
        align-items: center;
    `
)
const CustomTextareaAutosize = styled(TextareaAutosize)`
  padding: 8px;
  width: 100%;
  border-radius: 5px;
  border: 1px solid lightgrey;
  font-family: 'Pretendard';
`

const TextField = (props: TextFieldProps) => <MuiTextField size="small" required fullWidth {...props} />
